<template>
  <div class="login-page">
    <el-card>
      <template #header>黑马面经运营后台</template>
      <el-form
        ref="loginform"
        autocomplete="off"
        :model="formData"
        :rules="rules"
      >
        <el-form-item label="用户名" prop="username">
          <el-input
            placeholder="输入用户名"
            v-model="formData.username"
          ></el-input>
        </el-form-item>

        <el-form-item label="密码" prop="password">
          <el-input
            type="password"
            placeholder="输入用户密码"
            v-model="formData.password"
            show-password
          ></el-input>
        </el-form-item>

        <el-form-item class="tc">
          <el-button type="primary" @click="logining">登 录</el-button>
          <el-button @click="$refs['loginform'].resetFields();">重 置</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  name: 'LoginIndex',
  data () {
    return {
      formData: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          {
            required: true,
            message: '请输入用户名',
            trigger: ['blur', 'change']
          },
          {
            min: 5,
            max: 11,
            message: '长度在 5 到 11 个字符',
            trigger: ['blur', 'change']
          }
        ],
        password: [
          {
            required: true,
            message: '请输入密码',
            trigger: ['blur', 'change']
          },
          {
            pattern: /^\w{5,11}$/,
            message: '请输入 5 到 10 位的密码',
            trigger: ['blur', 'change']
          }
        ]
      }
    }
  },
  methods: {
    ...mapActions('user', ['loginAction']),
    async logining () {
      // await 语法糖写法(推荐)
      await this.$refs.loginform.validate()
      this.loginAction(this.formData)
      // promise写法
      /* this.$refs.loginform.validate().then(() => {
        console.log('准备登录')
      }).catch(() => {
        console.log('校验没有通过')
      }) */

      // 回调函数写法
      /* this.$refs.loginform.validate((valid) => {
        if (valid) {
          console.log('准备登录')
        }
      }) */
    }
  }
}
</script>

<style lang='scss' scoped>
.login-page {
  min-height: 100vh;
  background: url(@/assets/login-bg.svg) no-repeat center / cover;
  display: flex;
  align-items: center;
  justify-content: center;
  .el-card {
    width: 420px;
    ::v-deep .el-card__header {
      height: 80px;
      line-height: 40px;
      background: rgba(114, 124, 245, 1);
      text-align: center;
      color: #fff;
      font-size: 18px;
    }
  }
  .el-form {
    padding: 0 20px;
  }
  .tc {
    text-align: center;
  }
}
</style>
